const app = getApp();
export default {
	data() {
		return {
			info: {},
			imgUrl: '',
			dataType: '',
			usePoints: '',
			addressList: {},
			is_use: '2'
		};
	},
	onShow() {
		var arr = uni.getStorageSync('address') || [];
		// // 更新数据
		this.addressList = arr;
	},
	onLoad(option) {
		this.imgUrl = this.$helper.imgUrl;
		this.shopId = option.id;
		if (option.type == 'courseBuy') {
			this.dataType = 'curricula';
			this.title = '课程购买';
		} else if (option.type == 'books') {
			this.dataType = 'book';
			this.title = '图书购买';
		} else if (option.type == 'datas') {
			this.dataType = 'data';
			this.title = '资料购买';
		}
		this.type = option.type;
		uni.setNavigationBarTitle({
			title: this.title
		});
		this.getShop();
		this.submintOrder= this.$helper.debounce(this.submintPay, 1500)
	},
	methods: {
		getShop() {
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}pay/calculationPrice`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: this.dataType,
					id: this.shopId
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.info = res.data;
						this.usePoints = res.data.use_integral;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		checkboxChange(e) {
			let isType = e.detail.value;
			console.log(isType);
			if (isType == 1) {
				this.usePoints = 0;
				this.is_use = 1;
			} else {
				this.usePoints = 1;
				this.is_use = 2;
			}
		},
		goAdderss() {
			uni.navigateTo({
				url: '/pages/addersList'
			});
		},
		addAddress() {
			uni.navigateTo({
				url: '/pages/addersList'
			});
		},
		submintPay() {
			let address_id = '';
			if (this.info.type == 1) {
				address_id = this.addressList.id;
			}else{
				address_id = ''
			}
			uni.request({
				url: `${this.$helper.requestUrl}pay/placeOrder`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: this.dataType,
					id: this.shopId,
					is_use: this.is_use,
					address_id: address_id
					// discover_id: this.findId
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '下单信息');
					uni.hideToast()
					if (res.status_code == 'ok') {
						this.goBuy(res.data)

					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		goBuy(orderId){
			uni.request({
				url: `${this.$helper.requestUrl}pay/pay`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: this.dataType,
					ordernum: orderId,
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '-------------------');
					uni.hideToast()
					if (res.status_code == 'ok') {
						this.getPay(res.data);
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		getPay(payNode) {
			uni.showLoading({
				title: '支付加载中...',
			})
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: payNode.timeStamp,
				nonceStr: payNode.nonceStr,
				package: payNode.package,
				signType: payNode.signType,
				paySign: payNode.paySign,
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res)
					console.log(res)
					// console.log('success:' + JSON.stringify(res));
					uni.showToast({
						title: "支付成功",
						icon: 'success',
						duration: 2000
					});
					// 两秒后返回上一页
					setTimeout(e => {
						if (this.title == '课程购买') {
							uni.redirectTo({
								url:'/pages/courseOrder'
							})
						} else if (this.title == '图书购买') {
							uni.redirectTo({
								url:`/pages/dataOrder?type=图书订单`
							})
						} else if (this.title == '资料购买') {
							uni.redirectTo({
								url:`/pages/dataOrder?type=资料订单`
							})
						}
					}, 2000)
				},
				fail: err => {
					uni.showToast({
						title: "支付失败",
						icon: 'none',
						duration: 2000
					});
				}
			});
		}
	}
};
